@import "vars";
@import "mixins";

* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }

html {
    background-color: $dark-grey;
    font-family: $font;
    font-size: $body-size; }

body {
    font-size: $body-size;
    font-family: $font;
    color: $body-text;
    line-height: 2rem;
    letter-spacing: 1.5px;
    text-shadow: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    opacity: 1; }

b, strong {
    font-weight: 900;
    color: $body-text; }

main {
    padding: 1rem;
    @media screen and (min-width: 1024px) {
        padding-top: 4rem; } }

header {
    margin-bottom: 1rem;
    padding: 1.25rem 0 1.25rem 0;
    font-size: 2rem; }

h1,h2,h3 {
    font-family: 'Didact Gothic', sans-serif; }

ul {
    list-style-type: none; }

p {
    display: inline;
    color: darken($body-text, 10%); }

a {
    color: $links;
    text-decoration: none;
    border-bottom: 1px solid $links;
    @include ease(0.5s);
    &:hover {
        color: $links-hover;
        font-style: inherit; }
    &:active {
        color: $links; } }

button {
    padding: 20px;
    border: 0px;
    border-radius: 5px;
    background: $white;
    &:hover {
        box-shadow: 5px 5px $black; } }

hr {
    @include skinny-hr;
    opacity: 0.3; }

.body-hr {
    margin: auto;
    margin-top: 2rem;
    margin-bottom: 2rem;
    background-color: transparent;
    color: white;
    width: 65%;
    border-style: solid;
    border-width: 0.5pt; }

.wrap {
    margin: 0 auto;
    max-width: 64em;
    width: 65%;

    @media screen and (max-width: 1024px) {
        width: 80%; }
    @media screen and (max-width: 736px) {
        width: 90%; }
    @media screen and (max-width: 480px) {
        width: 100%;
        article ul {
            padding-left: 1rem; } } }

.tag-container {
    display: inline;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center; }

.tag {
    background-color: transparent;
    position: relative;
    bottom: 0; }

.flex-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start; }

.top-menu, .bottom-menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center; }

.bottom-menu {
    padding-top: 3rem; }

.top-menu {
    margin: 0;
    font-size: 1rem;
    align-self: center; }

.footer {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: transparent;
    position: relative;
    bottom: 0;
    padding: 1rem;
    font-family: $font;
    font-size: 1rem;
    line-height: 1em;
    opacity: 0.8;
    a {
        border-bottom: none;
        display: contents; } }

@import "custom";
@import "elements";


.go-left {
    justify-content: flex-start;
    align-items: flex-start; }

.go-right {
    justify-content: flex-end;
    align-items: flex-end; }

.go-center {
    justify-content: center;
    align-items: center; }
