p > img {
    align-self: center; }

.toc {
    margin: 0;
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding: 1rem;
    border: 1px solid black;
    background-color: lighten($dark-grey, 5%);
    border-radius: 1rem;
    #toc-header {
        float: left; }
    #toc-toggle {
        float: right;
        border: none;
        cursor: pointer; }
    .toc-h2 {
        padding-left: 1em;
        a {
            font-style: italic;
            font-size: smaller; } } }

#splash {
    margin: auto 0;
    padding-top: 4rem;
    width: 75%;

    display: flex;
    flex-flow: row-reverse wrap;
    justify-content: space-evenly;
    min-height: calc(100vh - 6em);

    @media screen and (max-width: 736px) {
        width: 100%;
        height: auto;
        white-space: nowrap; } }

.big-link {
    font-size: 3rem;
    font-weight: 300;
    margin-bottom: 2rem;
    line-height: 1.5em; }

.post-title {
    margin: 0; }

.post-summary {
    margin: 0;
    letter-spacing: 1px;
    font-style: italic;
    font-size: smaller; }

aside {
    font-size: smaller; }

article {
    width: 100%;
    line-height: 2em;
    letter-spacing: 2px;
    div {
        width: 100%;
        height: 100%;
        margin-bottom: 1em; }
    h1 {
        font-size: 1.5rem;
        margin: 2rem 0 1rem 0;
        color: darken($body-text, 30%); }
    h2 {
        font-size: 1.25rem;
        margin: 1rem 0 0.5rem 0;
        color: darken($body-text, 10%); }
    h3 {
        font-size: 1rem;
        margin: 1rem 0 0 0;
        color: $body-text; }
    // & h1,h2,h3
    //  // margin: 1em 0em
    //  // text-align: right
    & p {
        display: block;
        margin-bottom: 1em;
        line-height: 1.5;
        letter-spacing: 1.5px; }
    & .highlight {
        width: 100%; }
    & pre {
        line-height: 1rem;
        font-size: 0.75rem;
        border-radius: 3px;
        padding: 1rem;
        overflow-x: auto;
        background-color: $code-block;
        width: 100%;
        margin-bottom: 1em;
        color: black; }
    & pre > code {
        border: none; }
    & code {
        font-family: 'Source Code Pro', monospace;
        padding: 0.1rem;
        letter-spacing: 0px;
        border-radius: 3px;
        border: 1px solid $links; }
    & img {
        max-width: 100%;
        display: flex;
        margin: 2rem auto;
        opacity: 1; }
    & .grid > img {
        margin: auto; }
    & figure {
        align-self: center; }
    & blockquote {
        border-left: 5px solid darken($maroon, 15%);
        // padding: 0.5rem 1rem 0.5rem 2rem
        padding: 0.5rem 1rem 0.5rem 2rem;
        margin-bottom: 1rem;
        font-size: smaller;
        font-style: italic;
        line-height: 1.25;
        // text-align: justify
        & a {
            color: $white; }
        & ul {
            margin-top: 1rem; }
        & li {
            list-style: disc; }
        > p {
            display: block; }
        > p:last-of-type {
            padding-bottom: 0;
            margin-bottom: 0; }
        .quote-attribution {
            padding-left: 2rem;
            font-style: normal; } }

    & table {
        width: 100%;
        border-collapse: collapse;
        margin: 1rem 0;
        overflow-x: auto;
        td, th {
            padding: 5px 5px;
            text-align: left;
            line-height: 1.2; }
        td {
            font-size: 0.9rem;
            vertical-align: top; }
        th {
            border-bottom: 1px solid $links; }
        td {
            border-top: 1px solid $links; } }

    & li {
        line-height: 1.25;
        padding-left: 0.4rem;
        margin-bottom: 1.5rem; }
    & ul {
        list-style: disc outside;
        padding-left: 2rem; }
    & ol {
        list-style: decimal outside;
        padding-left: 2rem; }
    & .gist {
        max-width: 100%;
        td, th {
            text-align: left;
            border: 0; }
        & .gist-meta {
            background-color: transparent;
            color: $body-text;
            & a {
                color: $body-text; } } }

    // the second paragraph on the page
    & > p:not(.subtitle):nth-of-type(2)::first-letter,
    // the first paragraph after a heading
    & h1 + p::first-letter {
        font-family: serif;
        font-size: 300%;
        line-height: 1;
        letter-spacing: 1.5px;
        color: $body-text;
        float: left;
        padding-right: 0.1em;
        padding-top: 0.21em; }

    figcaption {
        text-align: center;
        margin: auto;
        width: 400px;
        margin-top: -1rem;
        & p {
            font-style: italic;
            font-size: smaller;
            line-height: 1rem; } } }

#metadata {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start; }

.subtitle {
    margin: 0 0 1rem 0;
    line-height: 1rem;
    letter-spacing: 1px;
    font-size: smaller;
    font-style: italic; }

.tag {
    padding: 0.25em 0em;
    opacity: 0.6; }

.tag-container {
    margin-left: 0.25rem; }

.grid {
    margin: 1em auto;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    @media screen and (max-width: 736px) {
        grid-template-columns: repeat(auto-fit, minmax(100%, 1fr)); } }

.two {
    grid-template-columns: 1fr 1fr; }

.grid > div {
    background-color: transparent;
    overflow: hidden; }

.grid > div > img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    opacity: 1; }

.grid > div > a > img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    opacity: 1; }

.footnotes {
    font-size: 9pt;
    ol > li {
        line-height: 1.75em !important; }
    li {
        margin-bottom: 1.5em; } }

.blurb {
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: smaller;

    .blurb-text {
        margin-bottom: 0.5rem; }

    .blurb-date {
        font-style: italic;
        margin-left: 3rem;
        font-size: smaller;
        &:before {
            content: "â€”"; } } }
