.notice {
    background-color: $notice;
    color: $dark-grey;
    padding: 0.75rem;
    border-radius: 3px;
    font-size: 0.8rem;
    text-align: justify;
    line-height: 1.5;
    letter-spacing: 1px;
    p {
        color: $dark-grey;
        display: block; }
    b, strong {
        font-weight: 900;
        color: $dark-grey; }
    a {
        color: lighten($dark-grey, 20);
        &:hover {
            color: $black; } } }

.warning {
    background-color: $warning !important; }

.info {
    background-color: $info !important; }

a.footnote-ref, a.footnote-backref {
    border: none; }

#container {
    width: 100%;
    max-width: 800px; }

.row {
    display: flex;
    flex-direction: row;
    justify-content: left;
    padding-left: 10%;
    padding-right: 10%; }

#pic {
    flex-basis: 20%;
    align-self: center; }

@media screen and (max-width: 590px) {
    #pic {
        margin-top: 8rem;
        margin-left: -3rem;
        img {
            align-self: center;
            width: 100px;
            height: 100px; } } }

@media screen and (max-width: 321px) {
    #container > .row {
        flex-direction: column; } }
